import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, ComponentRef, Injector, Type } from '@angular/core';
import { ProjectComponent } from './views/project/project.component';
import { ProjectWizardPackageView } from './views/projectwizardpackageview';
import { ProjectImportWizardPackageView } from './views/projectimportpackageview';
import { ProjectimportComponent } from './views/projectimport/projectimport.component';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';

export class ProjectWizardPackage extends Package {
  // modalPanel: ModalPanel;
  metadataService: GSPMetadataService;
  constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(gsp);
    this.metadataService = this.injector.get(GSPMetadataService);
  }

  activate(state) {
    this.gsp.eventBus.on(null, null, 'Command:newFrontProject', this, () => this.showModal(3, state, 'Create Form Project'));
    this.gsp.eventBus.on(null, null, 'Command:newProject', this, () => this.showModal(1, state, 'Create GSP Project'));
    this.gsp.eventBus.on(null, null, 'Command:deleteProject', this, () => this.deleteProject());
    this.gsp.eventBus.on(null, null, 'Command:newProjectImport', this, () => this.showModal(2, state, 'Import Project'));
  }

  addNewTreeNode(nodeInfo) {
    console.log(nodeInfo);
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', nodeInfo, () => {
      console.log('event has been deal done');
    });
  }

  refreshFileTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', {action: '5'}, () => {
      console.log('event has been deal done');
    });
  }

  deleteFileTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', {action: '4'}, () => {
      console.log('event has been deal done');
    });
  }

  showModal(tag: number, state: any, title: string) {
    // const commonComponent = <ModelcommonComponent>this.packageView.getElement().instance;
    if (tag === 1) {
      const packageView = new ProjectWizardPackageView(state, this.cfr, this.injector);
      const ele = packageView.getElement();
      const comp = ele.instance as ProjectComponent;
      comp.createProjectSuccess.subscribe((nodeInfo) => {
        if (nodeInfo === 1) {
          this.refreshFileTree();
        } else {
          this.addNewTreeNode(nodeInfo);
        }
      });
      this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
        console.log(nodeInfo);
        comp.setProjType('backend');
        if (!nodeInfo.nodePath) {
          comp.setPath('/');
        }
        if (nodeInfo.nodeType === 'folder' || nodeInfo.nodeType === 'project') {
          setTimeout(() => comp.setPath(nodeInfo.nodePath), 100);
        } else if (nodeInfo.nodeType === 'file') {
          let p = nodeInfo.nodePath.replace(/\\/g, '/');
          p = p.substring(0, p.lastIndexOf('/'));
          comp.setPath(p);
        } else {
          comp.setPath('/');
        }
        const modalPanel = this.gsp.workspace.addModalPanel({item: {
          element: ele,
          modalOptions: {
            title: title,
            width: 500,
            height: 525,
            buttons: comp.btns
          }
        }});
        comp.modalClosed.subscribe(() => modalPanel.destroy());
      });
    } else if (tag === 2) {
      const packageView = new ProjectImportWizardPackageView(state, this.cfr, this.injector);
      const ele = packageView.getElement();
      const comp = ele.instance as ProjectimportComponent;
      comp.importProjectSuccess.subscribe((nodeInfo) => {
        this.refreshFileTree();
        // this.addNewTreeNode(nodeInfo);
      });
      const height1 = window.innerHeight;
      const modalPanel = this.gsp.workspace.addModalPanel({
        item: {
          element: ele,
          modalOptions: {
            title: title,
            width: 730,
            height: height1 - 15 > 700 ? 700 : height1 - 15,
            buttons: comp.btns
          }
        }
      });
      comp.modalClosed.subscribe(() => modalPanel.destroy());
    } else if (tag === 3) {
      const packageView = new ProjectWizardPackageView(state, this.cfr, this.injector);
      const ele = packageView.getElement();
      const comp = ele.instance as ProjectComponent;
      comp.createProjectSuccess.subscribe((nodeInfo) => {
        if (nodeInfo === 1) {
          this.refreshFileTree();
        } else {
          this.addNewTreeNode(nodeInfo);
        }
      });
      this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
        console.log(nodeInfo);
        comp.setProjType('frontend');
        if (!nodeInfo.nodePath) {
          comp.setPath('/');
        }
        if (nodeInfo.nodeType === 'folder' || nodeInfo.nodeType === 'project') {
          setTimeout(() => comp.setPath(nodeInfo.nodePath), 100);
        } else if (nodeInfo.nodeType === 'file') {
          let p = nodeInfo.nodePath.replace(/\\/g, '/');
          p = p.substring(0, p.lastIndexOf('/'));
          comp.setPath(p);
        } else {
          comp.setPath('/');
        }
        const modalPanel = this.gsp.workspace.addModalPanel({
          item: {
            element: ele,
            modalOptions: {
              title: title,
              width: 500,
              height: 525,
              buttons: comp.btns
            }
          }
        });
        comp.modalClosed.subscribe(() => modalPanel.destroy());
      });
    }
  }

  deleteProject() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
      console.log(nodeInfo);
      if (nodeInfo.nodeType === 'project' || nodeInfo.nodeType === 'folder') {
        this.metadataService.DeleteProject(nodeInfo.nodePath.substring(1)).subscribe(data => {
          alert('删除成功');
          // 删除树节点
          this.deleteFileTree();
        }, error => {
          alert('删除失败');
        });
      } else {
        alert('请选择工程目录删除！');
      }
    });
  }
}



