import { Injectable } from '@angular/core';
import { HttpClient, HttpHandler, HttpHeaders } from '@angular/common/http';
import { Subject } from 'rxjs/Subject';
import { environment } from '../../environments/environment';
import { ProjectImporters } from './projectimporters';
import { ProjectImportersAdapter } from './projectimportersadapter';
import { catchError, map, tap } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { Headers, RequestOptions, Http } from '@angular/http';
import { ProjectOptions } from './project';

const SERVER_IP: string = environment.SERVER_IP + '/';
const PROJECTS_IMPORTERS = 'api/dev/main/v1.0/project-importers';
const PROJECTS_IMPORT = 'api/dev/main/v1.0/project/import';

@Injectable()
export class ProjectService {
  constructor(private httpClient: HttpClient, private http: Http) { }

  private getImportersUrl() {
    return SERVER_IP + PROJECTS_IMPORTERS;
  }

  private getImportProjUrl() {
    return SERVER_IP + PROJECTS_IMPORT;
  }
  private getRootPathUrl() {
    return SERVER_IP + 'api/dev/main/v1.0/project/rootpath';
  }

  public getRootPath() {
    const url = this.getRootPathUrl();
    const importers = new Subject<string>();
    this.httpClient.get(url, { 'responseType': 'text' }).subscribe(
      result => {
        console.log(result);
        if (result) {
          importers.next(result.toString());
        }
      });
    return importers;
  }

  public getImportTypes() {
    const url = this.getImportersUrl();
    const importers = new Subject<ProjectImporters[]>();
    this.httpClient.get(url).subscribe(
      result => {
        const adapter = new ProjectImportersAdapter(result);
        const data = adapter.getImporters();
        if (data) {
          importers.next(data);
        }
      });
    return importers;
  }

  public importProject(wspath: string, body: any): Observable<any> {
    const url = this.getImportProjUrl() + '/' + wspath;
    console.log(url);
    return this.httpClient.post<any>(url, body);
  }

  // 新建工程
  public createProject(options: ProjectOptions) {
    const content = {
      'type': options.type,
      'attributes': options.attributes,
      'path': options.path,
      'description': options.description,
      'name': options.name
    };
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', 'as');
    headers.append('Access-Control-Allow-Origin', '*');
    const resOptions = new RequestOptions({
      params: {},
      headers: headers
    });
    const url = SERVER_IP + 'api/dev/main/v1.0/project?name=' + options.name + '&nameSpace=' + options.nameSpace +
      '&packageName=' + options.packageName + '&targetFramework=' + options.targetFramework + '&conditionRelease=' +
       options.conditionRelease + '&conditionDebug=' + options.conditionDebug + '&outputPath=' + options.outputPath +
        '&boid=' + options.boID + '&appCode=' + options.appCode + '&suCode=' + options.suCode + '&boCode=' +
         options.boCode + '&suDeploymentPath=' + options.suDeploymentPath + '&projType=' + options.projType;
    return this.http.post(url, content, resOptions);
  }

  // 获取SU部署路径
  getSuDeploymentPath(appCode: string, suCode: string) {
    return 'apps/' + appCode + '/' + suCode;
  }
}



