import { Component, ViewChild, OnInit, TemplateRef, EventEmitter } from '@angular/core';
import { ProjectService } from '../../project.service';
import { Observable, Subject } from 'rxjs';
import { SelectableSettings, RowArgs } from '@progress/kendo-angular-grid';
import { NotifyService, NotifyOptions } from '@farris/ui';

@Component({
  selector: 'app-projectimport',
  templateUrl: './projectimport.component.html',
  styleUrls: ['./projectimport.component.css']
})

export class ProjectimportComponent implements OnInit {
  @ViewChild('btn') btns: TemplateRef<any>;
  public selectableSettings: SelectableSettings;
  importProjectSuccess = new EventEmitter();
  modalClosed = new EventEmitter();
  cols = [
    { field: 'id', header: '导入方式' }
  ];
  importersData = [];
  selectedItem = { id: 'git', 'description': ''};
  importRecursivelyFlag = false;
  branchFlag = false;
  remoteUrl = '';
  description = '';
  projectName = '';
  userName = '';
  password = '';
  branchName = '';
  skipRoot = false;
  isProjectShown: boolean;
  branchEnable = false;
  msgs = [];
  isLoading = false;
  rowGroupMetadata: any;

  constructor(private projectService: ProjectService, private notifyService: NotifyService) {
    this.selectableSettings = {
      checkboxOnly: false,
      mode: 'single',
      enabled: true
    };

    this.projectService.getImportTypes().subscribe(
      result => {
        this.importersData = result;
        if (this.importersData) {
          this.importersData = this.importersData.sort((a, b) => a.category - b.category);
        }
        this.selectedItem = result.find(res => res.default);
        // this.updateRowGroupMetaData();
      }
    );
  }
  public isRowSelected = (e: RowArgs) => e.dataItem.id === this.selectedItem.id;
  public selectionChange(event: any) {
    if (event.selectedRows) {
      this.selectedItem = event.selectedRows[0].dataItem;
    }
  }
  ngOnInit() {
  }
  onUrlChanged(event) {
    console.log(event);
  }
  // 新建工程
  importProject(): Observable<any> {
    const importState = new Subject<Boolean>();
    if (!this.validateData()) {
      return importState;
    }
    const parameters = {};
    if (this.selectedItem.id === 'git' || this.selectedItem.id === 'github') {
      parameters['username'] = this.userName;
      parameters['password'] = this.password;
      if (this.branchFlag) {
        parameters['branch'] = this.branchName;
      }
      if (this.importRecursivelyFlag) {
        parameters['recursive'] = this.importRecursivelyFlag;
      }
    } else {
      parameters['skipFirstLevel'] = this.skipRoot;
    }
    const body = {
      'location': this.remoteUrl,
      'type': this.selectedItem.id,
      parameters
    };
    this.isLoading = true;
    this.projectService.importProject(this.projectName, body).subscribe(
      result => {
        console.log('success');
        // this.notifyService.success(<NotifyOptions>{ title: '系统提示', msg: '创建成功。', timeout: 1000 });
        alert('创建成功。');
        this.isLoading = false;
        importState.next(true);
        this.importProjectSuccess.emit({
          action: '1',
          node: {
            nodeID: this.projectName,
            nodeName: this.projectName,
            nodePath: '/' + this.projectName,
            nodeType: 'project'
          }
        });
        // close
        this.modalClosed.emit();
      },
      error => {
        this.isLoading = false;
        console.log('error: ' + error);
        // this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '创建失败。', timeout: 1000 });
        alert('创建失败，内部错误。');
        importState.next(false);
      }
    );
    return importState;
  }

  // 校验
  validateData(): boolean {
    if (!this.selectedItem || !this.selectedItem.id) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'Please select an import type.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '请选择导入方式。', timeout: 1000 });
      return false;
    }
    if (!this.remoteUrl) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'URL can not be empty.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: 'URL地址不能为空。', timeout: 1000 });
      return false;
    }
    if (!this.projectName) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'Project Name can not be empty.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '工程名不能为空。', timeout: 1000 });
      return false;
    }
    if (this.selectedItem.id === 'git' && this.branchEnable === true && !this.branchName) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'Branch Name can not be empty.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '分支名不能为空。', timeout: 1000 });
      return false;
    }
    if (this.selectedItem.id === 'git' && !this.userName) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'User Name can not be empty.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '用户名不能为空。', timeout: 1000 });
      return false;
    }
    if (this.selectedItem.id === 'git' && !this.password) {
      // this.msgs = [{ severity: 'info', summary: 'Notification', detail: 'Password can not be empty.' }];
      this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '密码不能为空。', timeout: 1000 });
      return false;
    }
    return true;
  }

  // 填写分支名
  onBranchFlagChanged(checked) {
    if (checked) {
      this.branchEnable = true;
    } else {
      this.branchEnable = false;
    }
  }

  clickOK() {
    this.importProject();
  }

  clickCancel() {
    this.modalClosed.emit();
  }

  /*
  onSort() {
    this.updateRowGroupMetaData();
  }

  updateRowGroupMetaData() {
    this.rowGroupMetadata = {};
    if (this.importersData) {
      for (let i = 0; i < this.importersData.length; i++) {
        const rowData = this.importersData[i];
        const category = rowData.category;
        if (i === 0) {
          this.rowGroupMetadata[category] = { index: 0, size: 1 };
        } else {
          const previousRowData = this.importersData[i - 1];
          const previousRowGroup = previousRowData.category;
          if (category === previousRowGroup) {
            this.rowGroupMetadata[category].size++;
          } else {
            this.rowGroupMetadata[category] = { index: i, size: 1 };
          }
        }
      }
    }
  }
  */
}



