// tslint:disable-next-line:max-line-length
import { Component, ViewChild, OnInit, TemplateRef, EventEmitter, ComponentFactory, ComponentFactoryResolver, Injector } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ProjectService } from '../../project.service';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { CacheService } from '@farris/ide-devkit/services';
import { BusinessObjectsSelectComponent } from '@gsp-lcm/bo-selector';
import { BsModalService } from '@farris/ui';
import { environment } from '../../../../environments/environment';
import { NotifyService, NotifyOptions } from '@farris/ui';
import { ProjectOptions } from '../../project';

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})
export class ProjectComponent implements OnInit {
  private prefix = 'Inspur.GS.';
  selectBO: EventEmitter<any> = new EventEmitter();
  @ViewChild('btn') btns: TemplateRef<any>;
  modalClosed = new EventEmitter();
  createProjectSuccess = new EventEmitter<any>();
  rootPath = '';
  projType = 'backend';
  constructor(private cache: CacheService, private projService: ProjectService,
    private metadataservice: GSPMetadataService, private modalService: BsModalService,
    private resolver: ComponentFactoryResolver, private injector: Injector, private notifyService: NotifyService) {
    this.projService.getRootPath().subscribe(path => {
      this.rootPath = path;
    });
  }
  private sessionId = this.cache.get('sessionId');
  project = {
    path: '/',
    appCode: '',
    suCode: '',
    boCode: '',
    boID: '',
    suDeploymentPath: '',
    name: '',
    nameSpace: '',
    targetFramework: 'netcoreapp2.0',
    conditionRelease: "\"'$(Configuration)|$(Platform)'=='Release|AnyCPU'\"",
    conditionDebug: "\"'$(Configuration)|$(Platform)'=='Debug|AnyCPU'\"",
    outputPath: 'bin\\',
    packageName: '',
    description: ''
  };

  ngOnInit() {
    // this.isProjectShown = false;
  }
  setPath(path: string) {
    this.project.path = path.replace(/\\/g, '/');
  }
  setProjType(projType: string) {
    this.projType = projType;
  }
  setAppCode() {
    if (this.project.suCode && this.project.boCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    } else if (this.project.suCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode;
    } else if (this.project.boCode) {
      this.project.nameSpace = this.prefix + this.project.appCode +  '.' + this.project.boCode;
    } else {
      this.project.nameSpace = this.prefix + this.project.appCode;
    }
    // this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    this.project.packageName = this.project.nameSpace;
  }
  setSuCode() {
    if (this.project.appCode && this.project.boCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    } else if (this.project.appCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode;
    } else if (this.project.boCode) {
      this.project.nameSpace = this.prefix + this.project.suCode +  '.' + this.project.boCode;
    } else {
      this.project.nameSpace = this.prefix + this.project.suCode;
    }
    // this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    this.project.packageName = this.project.nameSpace;
  }
  setBoCode() {
    if (this.project.appCode && this.project.suCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    } else if (this.project.appCode) {
      this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.boCode;
    } else if (this.project.suCode) {
      this.project.nameSpace = this.prefix + this.project.suCode +  '.' + this.project.boCode;
    } else {
      this.project.nameSpace = this.prefix + this.project.boCode;
    }
    // this.project.nameSpace = this.prefix + this.project.appCode + '.' + this.project.suCode + '.' + this.project.boCode;
    this.project.packageName = this.project.nameSpace + (this.projType === 'frontend' ? '.front' : '');
    this.project.name = 'bo-' + this.project.boCode + (this.projType === 'frontend' ? '-front' : '');
  }

  // 新建工程
  createProject(): Observable<any> {
    const importState = new Subject<Boolean>();
    const selectType = 'folder'; // this.workspace.fileTreeActivedNodeType;
    if (selectType === undefined || selectType === 'folder' || selectType === 'project') {
      const attributes = {};
      const type = 'gs';
      const name = this.project.name;
      const reg = new RegExp('^[a-zA-Z-.\x7f-\xff][a-zA-Z0-9-.\x7f-\xff]*$');
      const reg2 = new RegExp('^[a-zA-Z0-9.\x7f-\xff]*$');
      if (!name) {
        alert('工程名称不能为空！');
        return importState;
      } else if (!reg.test(name)) {
        alert('工程名称包含非法字符，请重新输入');
        return importState;
      }
      const appCode = this.project.appCode;
      if (!appCode) {
        alert('关键应用不能为空！');
        return importState;
      }
      const suCode = this.project.suCode;
      if (!suCode) {
        alert('服务单元不能为空！');
        return importState;
      }
      const boCode = this.project.boCode;
      if (!boCode) {
        alert('业务对象不能为空！');
        return importState;
      }
      if (!this.project.boID) {
        this.project.boID = boCode;
      }
      const nameSpace = this.project.nameSpace;
      if (!nameSpace) {
        alert('命名空间不能为空！');
        return importState;
      } else if (!reg2.test(nameSpace)) {
        alert('工程命名空间包含非法字符，请重新输入');
        return importState;
      }
      const packageName = this.project.packageName;
      if (!packageName) {
        alert('包名不能为空！');
        return importState;
      } else if (!reg2.test(packageName)) {
        alert('元数据包名包含非法字符，请重新输入');
        return importState;
      }
      let path = '';
      if (this.project.path === '/') {
        path = this.project.path + name;
      } else {
        path = this.project.path + '/' + name;
      }
      const description = this.project.description;

      // 部署路径与配置文件路径检测
      if (!this.project.suDeploymentPath) {
        this.project.suDeploymentPath = this.projService.getSuDeploymentPath(this.project.appCode, this.project.suCode);
      }
      const options = new ProjectOptions();
      options.appCode = this.project.appCode;
      options.projType = this.projType;
      options.suDeploymentPath = this.project.suDeploymentPath;
      options.boID = this.project.boID;
      options.type = type;
      options.attributes = attributes;
      options.path = path;
      options.description = description;
      options.name = name;
      options.nameSpace = nameSpace;
      options.packageName = packageName;
      options.boCode = this.project.boCode;
      options.targetFramework = this.project.targetFramework;
      options.conditionRelease = this.project.conditionRelease;
      options.conditionDebug = this.project.conditionDebug;
      options.outputPath = this.project.outputPath;
      options.suCode = this.project.suCode;

      this.projService.createProject(options).subscribe(
        result => {
          // this.notifyService.success(<NotifyOptions>{ title: '系统提示', msg: '新建成功。', timeout: 1000 });
          alert('新建成功。');
          importState.next(true);
          if (this.project.path === '/') {
            this.createProjectSuccess.emit(1);
          } else {
            this.createProjectSuccess.emit({
              action: '1',
              node: {
                nodeID: name,
                nodeName: name,
                nodePath: this.project.path + '/' + name,
                nodeType: 'project'
              }
            });
          }
          this.modalClosed.emit();
        },
        error => {
          alert('新建失败。');
          // this.notifyService.error(<NotifyOptions>{ title: '系统提示', msg: '新建失败。', timeout: 1000 });
          importState.next(false);
        }
      );
      return importState;
    } else {
      alert('请选择合适的存放目录');
    }
    this.project = {
      path: '/',
      appCode: '',
      suCode: '',
      boCode: '',
      boID: '',
      suDeploymentPath: '',
      name: '',
      nameSpace: '',
      targetFramework: 'netcoreapp2.0',
      conditionRelease: "\"'$(Configuration)|$(Platform)'=='Release|AnyCPU'\"",
      conditionDebug: "\"'$(Configuration)|$(Platform)'=='Debug|AnyCPU'\"",
      outputPath: 'bin\\',
      packageName: '',
      description: ''
    };
    return importState;
  }

  clickOK() {
    this.createProject();
  }
  onSelectBO() {
    // alert('on');
    this.selectBO.emit();
  }
  setBO(data: any) {
    this.metadataservice.GetDevBasicBOInfo(data.id, this.sessionId).subscribe(item => {
      this.project.appCode = item.appCode;
      this.project.suCode = item.suCode;
      this.project.boCode = item.boInfo.code;
      this.project.boID = item.boInfo.id;
      this.project.name = 'bo-' + item.boInfo.code + (this.projType === 'frontend' ? '-front' : '');
      // this.project.nameSpace = 'Inspur.GS.' + item.boInfo.appCode + '.' + item.boInfo.suCode + '.' + item.boInfo.boCode;
      this.project.nameSpace = item.boNameSpace + (this.projType === 'frontend' ? '.front' : '');
      this.project.packageName = this.project.nameSpace;
      this.project.suDeploymentPath = item.suInfo.applicationPath;
    });
  }

  clickCancel() {
    this.modalClosed.emit();
  }

  openBoSelect() {
    const compFactory = this.resolver.resolveComponentFactory(BusinessObjectsSelectComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.selectedBOEvent.subscribe($event => {
      console.log('$event', $event);
      this.setBO($event);
    });
    const modalConfig = {
      title: '业务对象选择',
      width: 400,
      height: 400,
      buttons: compRef.instance.btns,
      showButtons: true
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.modalClosed.subscribe($event => {
      dialog.close();
    });
  }

}



