import { ProjectImporters } from './projectimporters';

export class ProjectImportersAdapter {
    constructor(private importersReqResult: any) {

    }

    getImporters(importersReqResult: any = this.importersReqResult) {
        if (importersReqResult == null) { return null; }

        const projImps = [];

        const defaultTag = importersReqResult.configuration['default-importer'];

        importersReqResult.importers.forEach(imp => {
            const importer = new ProjectImporters();
            importer.id = imp.id.toLowerCase();
            importer.category = imp.category;
            importer.internal = imp.internal;
            importer.description = imp.description;
            importer.default = false;
            if (defaultTag && defaultTag === imp.id) {
                importer.default = true;
            }
            projImps.push(importer);
        });
        return projImps;
    }
}



